
#people-here{
    margin-top: 50px;
}

#people-here #label{
    background-color: #A6D0F1;
    display: inline-block;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    padding-left: 5px;
    padding-right: 5px;
    margin-left: 10px;
}

#people-here #label span{
    font-size: 12px;
    color: #235373;
    font-weight: 700;
}

#people-here #box{
    min-height: 100px;
    max-height: 350px;
    width: 150px;
    margin: 0;
    background-color: #FFFFFF;
    border: 5px solid #A6D0F1;
    border-radius: 5px;
    list-style: none;
    margin: 0px;
    padding: 0px;
}

#people-here #box li{
    padding: 5px;
    cursor: default;
    border-bottom: 1px solid rgb(0, 0, 0, 0.1);
}
